*, *::before, *::after {
  outline: 1px solid red;
}

.nav {
  position: fixed;
  min-width: 296px;
  min-height: 100vh;
  padding: 32px 0px;
  &-logo {
      background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/2b326fe971cd296bd54667a9b4a63c4b0f19fab3/week5/logo-ANSWER.svg);
      width: 151px;
      height: 40px;
      margin-bottom: 32px;
      margin-left: 32px;
  }
  &-item{
      margin-bottom: 20px;
  }
  &-link {
      color: $gray-900;
      // padding-top: $spacer*0.25;
      // padding-bottom: $spacer*0.25;
      padding-left: 32px;
      &:hover {
          color: $gray-900;
          background-color: $gray-400;
      }
    }
      

}
.material-icons {
  margin-right: $spacer;
}

