//全域設定
*, *::before, *::after {
    box-sizing: border-box;
}

body {
    font-family: $font-lato, $font-roboto-mono, $font-family-all;
    font-size: 16px;
    line-height: 1.5;
    color: $gray-900;    
  }
img {
    display: block;
}
a {
    display: block;
    text-decoration: none;
}